/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
h1,
h2,
h3,
p,
a,
form,
footer,
section {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

footer,
section {
  display: block;
}

body {
  line-height: 1;
}

* {
  box-sizing: border-box;
}

body {
  position: relative;
  background: #303168;
  font-family: "Roboto", sans-serif;
  color: white;
  line-height: 1.25;
  text-align: center;
  font-weight: 300;
  font-size: 1em;
}

.section--info {
  width: 90%;
  max-width: 550px;
  position: absolute;
  z-index: 10;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 5em auto;
}

@media screen and (min-width: 1500px) {
  .section--info {
    margin: 15em auto;
  }
}

.section--logotype {
  padding: 6em 0 4em 0;
  background-image: url(../images/imm2016logotype_small.svg);
  background-repeat: no-repeat;
  background-size: 35%;
  background-position: center;
}

@media screen and (max-width: 600px) {
  .section--logotype {
    padding: 2.4em 0;
  }
}

h1 {
  font-size: 3.375em;
  font-weight: 400;
  letter-spacing: 0.3em;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 2.4em;
  }
}

@media screen and (max-width: 370px) {
  h1 {
    letter-spacing: 0.1em;
  }
}

@media screen and (min-width: 1500px) {
  h1 {
    font-size: 4em;
  }
}

h2 {
  font-size: 2em;
  letter-spacing: 0.2em;
  font-weight: 300;
}

@media screen and (max-width: 600px) {
  h2 {
    font-size: 1.6em;
  }
}

h3 {
  font-size: 2em;
  letter-spacing: 0.2em;
  margin: 0.8em 0;
}

@media screen and (max-width: 600px) {
  h3 {
    font-size: 1.6em;
  }
}

a {
  color: white;
  text-decoration: none;
}

a:hover {
  border-bottom: 2px solid white;
}

a.social--icon:hover {
  opacity: 0.6;
  border-bottom: 0;
}

.section--details {
  font-size: 1em;
}

.container--date,
.container--location {
  display: inline-block;
  height: 20px;
  margin: 0 0 0 0.667em;
}

@media screen and (max-width: 600px) {
  .container--date,
  .container--location {
    margin-bottom: 0.667em;
  }
}

.container--location p::before {
  content: '';
  background-image: url(../images/pin.svg);
}

.container--date p::before {
  content: '';
  background-image: url(../images/calendar.svg);
}

.container--date p::before,
.container--location p::before {
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin: 0 0.2em 0 0;
}

form {
  display: flex;
  flex-direction: row;
  padding: 0.8em 0;
}

@media screen and (max-width: 500px) {
  form {
    display: block;
  }
}

input {
  -webkit-appearance: none;
}

input[type=email] {
  background: #3E3F76;
  border: 1px solid white;
  border-radius: 5px 0 0 5px;
  color: white;
  padding: 0.667em;
  width: 100%;
  font-size: 1em;
}

@media screen and (max-width: 500px) {
  input[type=email] {
    border-radius: 5px 5px 0 0;
  }
}

:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: white;
  opacity: 1;
  font-style: italic;
  font-weight: 300;
}

::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: white;
  opacity: 1;
  font-style: italic;
  font-weight: 300;
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
  font-style: italic;
  font-weight: 300;
}

input[type=submit] {
  padding: 0.4em 1.667em;
  border-radius: 0 5px 5px 0;
  background: white;
  color: #3E3F76;
  font-family: "Roboto", sans-serif;
  font-size: 1em;
  border: 2px solid white;
  cursor: pointer;
  text-align: center;
}

input[type=submit]:hover {
  color: #E94B87;
}

@media screen and (max-width: 500px) {
  input[type=submit] {
    width: 100%;
    border-radius: 0 0 5px 5px;
  }
}

@media screen and (max-width: 500px) {
  .line-break {
    display: block;
  }
}

.social--icon {
  display: inline-block;
  margin: 1.6em 0.4em;
}

#form-response {
  color: #E94B87;
}

#bg--threejs {
  position: fixed;
}
